ght: auto;\n    margin-left: auto;\n\n    max-height: 100%;\n    height: 100%;\n\n    padding: 0 15px;\n  }\n\n  &.open {\n    background: rgba(32, 41, 47, 0.9);\n    z-index: 1050;\n    visibility: visible;\n    cursor: default;\n\n    &.calendar-modal .modal1 {\n      padding-top: 0;\n    }\n\n    .modal1 {\n      transform: translateY(100px);\n      opacity: 1;\n\n      @include for-sm-screen {\n        transform: translateY(40px);\n      }\n    }\n  }\n\n  .modal1 {\n    margin-top: 0;\n    top: 0;\n    transform: translateY(0);\n    opacity: 0;\n    transition: all 0.3s ease-out;\n    overflow: hidden;\n    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.25);\n    border-radius: 15px;\n    padding: 0;\n\n    .modal-header {\n      background: transparent;\n      display: flex;\n      padding-bottom: 30px;\n      border-bottom: 1px solid var(--modal-header-line);\n      color: var(--modal-header-title);\n      font-size: $font-size-title-small;\n      font-weight: $font-weight-extra-bold;\n      justify-content: space-between;\n      align-items: center;\n\n      .icon-close {\n        @include hover-transition;\n        cursor: pointer;\n\n        svg {\n          @include hover-transition;\n          path { stroke: var(--modal-header-close); }\n        }\n\n        &:hover {\n          opacity: 0.7;\n        }\n      }\n    }\n\n    .modal-body {\n      display: flex;\n      flex-direction: column;\n      background: var(--modal-box-bgd);\n\n      .modal-header { padding: 23px 30px; }\n\n      .modal-body-content { padding: 23px 30px 30px 30px; }\n    }\n  }\n\n  @include for-sm-screen {\n    .modal1 .modal-body .modal-header {\n      padding: 20px;\n\n      .modal-title { font-size: $font-size-extra-large; }\n    }\n  }\n}\n","$main-font: 'Roboto', sans-serif;\n\n//FONT SIZES ( 1rem = 10px )\n$font-size-title-home-page: 4rem;\n$font-size-title: 3.5rem;\n$font-size-title-small: 2.5rem;\n$font-size-giant: 2.2rem;\n$font-size-extra-large: 2rem;\n$font-size-large: 1.8rem