ius: 10px;\n     background-color: rgba(0, 0, 0, 0.65);\n     border: 5px solid rgba(0, 0, 0, 0);\n     background-clip: padding-box;\n     -webkit-box-shadow: inset -1px -1px 0 rgba(0, 0, 0, 0.05), inset 1px 1px 0 rgba(0, 0, 0, 0.05);\n }\n\n.navbar-collapse.collapsing, .navbar-collapse.collapse.in {\n    border-bottom: 1px solid black;\n}\n\n.navbar-brand {\n    display: flex;\n    align-items: center;\n}\n\n.navbar-brand span {\n    margin-right: 10px;\n}\n\n.children-wrapper {\n    margin-top: 10px;\n}\n\n.marketplace-wrapper {\n  background: white;\n  display: flex;\n  margin-top: 20px;\n}\n\n.marketplace-play-wrapper {\n  height: 200px;\n  font-size: 40px;\n  justify-content: center;\n  align-items: center;\n  display: flex;\n}\n\n.marketplace-wrapper .songs-wrapper {\n  padding: 10px;\n  padding-left: 0;\n  width: 100%;\n  justify-content: flex-start;\n  display: flex;\n  flex-wrap: wrap;\n}\n\n.songs-count {\n  margin: 20px 0 5px 20px;\n  font-size: 16px;\n  font-weight: bold;\n}\n\n.single-song {\n  display: flex;\n  position:relative;\n  flex-direction: column;\n  width: 200px;\n  margin: 10px 10px 5px 10px;\n}\n\n.single-song #load, .single-song  .loading-icon-wrapper {\n  width: 45px;\n}\n\n.single-song #load #loading-inner {\n  width: 43px;\n  stroke: #fff;\n}\n\n.single-song .header-label {\n  position: absolute;\n  top: 10px;\n  left: 25px;\n  z-index: 10;\n  background: black;\n  width: 150px;\n  text-align: center;\n  padding: 5px;\n  font-size: 14px;\n  color: white;\n}\n\n.single-song .header-label span {\n  margin-right: 5px;\n}\n\n.single-song .overlay {\n  position: absolute;\n  transition: 0.2s ease-in;\n  display: flex;\n  visibility: hidden;\n  z-index: 10;\n  top: 0;\n  left: 0;\n  height: 200px;\n  width:100%;\n  align-items: center;\n  justify-content: space-evenly;\n}\n\n.overlay .play, .overlay .open, .overlay .stop {\n  color: white;\n  cursor: pointer;\n  font-size: 45px;\n}\n\n.jingle-image-container {\n  position: relative;\n  z-index: 9;\n  width:100%;\n  vertical-align:top;\n}\n\n.jingle-image-container .jingle-image-wrapper {\n  height: 200px;\n}\n\n.single-song .jingle-image-container:hover + .overlay, .single-song .overlay:hover {\n  visibility: visible;\n  background: rgba(0, 0, 0, 0.5);\n}\n\n/*img:after*/\n/*opacity: 1;*/\n\n.marketplace-wrapper .sidebar {\n  border-right: 1px solid #d1d1d1;\n  color: black;\n  padding: 20px;\n  padding-left: 0;\n  flex: 1;\n  flex-basis: 240px;\n  min-width: 240px;\n  max-width: 240px;\n  margin-right: 10px;\n  margin-bottom: 20px;\n}\n\n.button {\n  padding: 10px 0;\n  display: flex;\n  justify-content: center;\n  color: var(--button-white);\n  font-size: $font-size-medium;\n  border: none;\n  width: 100%;\n  border-radius: 5px;\n  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.251);\n  cursor: pointer;\n  @include hover-transition;\n\n  &:hover { opacity: 0.7; }\n\n  &.disconnect { background: var(--button-red); }\n}\n\n\n.sort-wrapper:not(:last-child) {\n  margin-bottom: 20px;\n}\n\n.sort-wrapper .sort-wrapper-label {\n  font-size: 20px;\n  margin-bottom: 15px;\n}\n\n.marketplace-page-wrapper .pagination-wrapper {\n  margin-left: 15px;\n}\n","$main-font: 'Roboto', sans-serif;\n\n//FONT SIZES ( 1rem = 10px )\n$font-size-title-home-page: 4rem;\n$font-size-title: 3.5rem;\n$font-size-title-small: 2.5rem;\n$font-size-giant: 2.2rem;\n$font-size-extra-large: 2rem;\n$font-size-large: 1.8rem;\n$font-size-medium: 1.6rem;\n$font-size-regular: 1.4rem;\n$font-size-small-regular: 1.3rem;\n$font-size-small: 1.2rem;\n$font-size-extra-small: 1rem;\n$font-size-tiny: 0.8rem;\n\n//FONT WEIGHTS\n$font-weight-extra-bold: 900;\n$font-weight-bold: 700;\n$font-weight-medium: 500;\n$font-weight-regular: 400;\n","$box-border-radius: 15px;\n$box-box-shadow: 0 0 15px var(--box-item-box-shadow);\n$box-padding: 20px 25px 25px 25px;\n\n@mixin for-sm-iphone {\n  @media only screen and (max-width: 320px) { @content; }\n}\n\n@mixin for-sm-phone {\n  @media only screen and (max-width: 360px)