x-width: 1000px) { @content; }\n}\n\n@mixin hover-transition {\n  transition: all 420ms cubic-bezier(.165,.84,.44,1);\n}\n\n@mixin hello($duration) {\n  @keyframes hello {\n    0% {\n      transform: translateY(-50px);\n      opacity: 0;\n    }\n    100% {\n      transform: translateY(0px);\n      opacity: 1;\n    }\n  }\n\n  animation: hello $duration;\n}\n\n@function vh($quantity) {\n  @return calc(var(--vh, 1vh) * #{$quantity});\n}\n\n@mixin rotate() {\n  @keyframes rotating {\n    0% { transform: rotate(360deg); }\n    100% { transform: rotate(1deg); }\n  }\n\n  animation: rotating 1s linear infinite;\n}\n\n@mixin flex-center {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n@mixin ellipsis {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  align-items: center;\n  white-space: nowrap;\n}\n\n@mixin inPage-provider-hover-border($name) {\n  @each $selectorName, $actualSelector in $provider-colors {\n    &.#{$selectorName} {\n      #{$name}: $actualSelector;\n\n      &:hover { border: 1px solid lighten($actualSelector, 10); }\n    }\n  }\n}\n"],"sourceRoot":""}]);
