n\n  animation: hello $duration;\n}\n\n@function vh($quantity) {\n  @return calc(var(--vh, 1vh) * #{$quantity});\n}\n\n@mixin rotate() {\n  @keyframes rotating {\n    0% { transform: rotate(360deg); }\n    100% { transform: rotate(1deg); }\n  }\n\n  animation: rotating 1s linear infinite;\n}\n\n@mixin flex-center {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n@mixin ellipsis {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  align-items: center;\n  white-space: nowrap;\n}\n\n@mixin inPage-provider-hover-border($name) {\n  @each $selectorName, $actualSelector in $provider-colors {\n    &.#{$selectorName} {\n      #{$name}: $actualSelector;\n\n      &:hover { border: 1px solid lighten($actualSelector, 10); }\n    }\n  }\n}\n","$browser: #323232;\n$mathWallet: #323232;\n$safePal: #323232;\n$tokenPocket: lighten(#2485F2, 10);\n$metaMask: #F89B55;\n$status: #4360DF;\n$imToken: #068EBD;\n$trust: #3476BB;\n$coinbase: #1253F5;\n$tokenary: #2C7CF5;\n$opera: #EA1227;\n\n$provider-colors: (\n  MetaMask: $metaMask,\n  browser: $browser,\n  Status: $status,\n  imToken: $imToken,\n  Trust: $trust,\n  Coinbase: $coinbase,\n  Tokenary: $tokenary,\n  Opera: $opera,\n  MathWallet: $mathWallet,\n  SafePal: $safePal,\n  TokenPocket: $tokenPocket,\n);\n","$main-font: 'Roboto', sans-serif;\n\n//FONT SIZES ( 1rem = 10px )\n$font-size-title-home-page: 4rem;\n$font-size-title: 3.5rem;\n$font-size-title-small: 2.5rem;\n$font-size-giant: 2.2rem;\n$font-size-extra-large: 2rem;\n$font-size-large: 1.8rem;\n$font-size-medium: 1.6rem;\n$font-size-regular: 1.4rem;\n$font-size-small-regular: 1.3rem;\n$font-size-small: 1.2rem;\n$font-size-extra-small: 1rem;\n$font-size-tiny: 0.8rem;\n\n//FONT WEIGHTS\n$font-weight-extra-bold: 900;\n$font-weight-bold: 700;\n$font-weight-medium: 500;\n$font-weight-regular: 400;\n"],"sourceRoot":""}]);
